import auth0 from 'auth0-js';
import jwt_decode from 'jwt-decode'

const JWTS_LOCAL_KEY = 'JWTS_LOCAL_KEY';

class Auth {
  constructor() {
    this.auth0 = new auth0.WebAuth({
      domain: 'dev-6id39aor.auth0.com',
      audience: 'castingapi',
      clientID: 'KdG7NgFNyBEdKo0SqE2WRWciKmyDSUqe',
      redirectUri: 'https://yonyalva.github.io/castingagency/callback',
      responseType: 'token id_token',
      scope: 'openid profile'
    });

    this.getProfile = this.getProfile.bind(this);
    this.handleAuthentication = this.handleAuthentication.bind(this);
    this.isAuthenticated = this.isAuthenticated.bind(this);
    this.signIn = this.signIn.bind(this);
    this.signOut = this.signOut.bind(this);
  }

  getProfile() {
    return this.profile;
  }

  getIdToken() {
    return this.idToken;
  }

  getToken() {
    return localStorage.getItem('JWTS_LOCAL_KEY');
  }

  getPermission() {
    return this.payload;
  }

  isAuthenticated() {
    return new Date().getTime() < this.expiresAt;
  }

  signIn() {
    this.auth0.authorize();
  }

   // invoked in app.component on load YA
  check_token_fragment() {
    // parse the fragment
    const fragment = window.location.hash.substr(1).split('&')[0].split('=');
    // check if the fragment includes the access token
    if ( fragment[0] === 'access_token' ) {
      // add the access token to the jwt
      this.token = fragment[1];
      // save jwts to localstore
      this.set_jwt();
    }
  }
  //  YA
  set_jwt() {
    localStorage.setItem(JWTS_LOCAL_KEY, this.token);
    if (this.token) {
      this.decodeJWT(this.token);
    }
  }

  load_jwts() {
    this.token = localStorage.getItem(JWTS_LOCAL_KEY) || null;
    if (this.token) {
      this.decodeJWT(this.token);
    }
  }

  decodeJWT(token: string) {
    // const jwtservice = new JwtHelperService();
    this.payload = jwt_decode(this.token);
    return this.payload;
  }

  handleAuthentication() {
    return new Promise((resolve, reject) => {
      this.auth0.parseHash((err, authResult) => {
        if (err) return reject(err);
        if (!authResult || !authResult.idToken) {
          return reject(err);
        }
        this.setSession(authResult);
        resolve();
      });
    })
  }

  setSession(authResult) {
    this.idToken = authResult.idToken;
    this.profile = authResult.idTokenPayload;
    // set the time that the id token will expire at
    this.expiresAt = authResult.idTokenPayload.exp * 1000;
  }

  signOut() {
    this.auth0.logout({
      returnTo: 'https://yonyalva.github.io/castingagency/',
      clientID: 'KdG7NgFNyBEdKo0SqE2WRWciKmyDSUqe',
    });
    localStorage.removeItem('JWTS_LOCAL_KEY');
  }

  silentAuth() {
    return new Promise((resolve, reject) => {
      this.auth0.checkSession({}, (err, authResult) => {
        if (err) return reject(err);
        this.setSession(authResult);
        resolve();
      });
    });
  }

  can(permission: string) {
    return this.payload && this.payload.permissions && this.payload.permissions.length && this.payload.permissions.indexOf(permission) >= 0;
  }

}

const auth0Client = new Auth();

export default auth0Client;
