import React, { Component } from 'react';
import $ from 'jquery';
import auth0Client from './Auth';
import { Link } from 'react-router-dom';

class Actor extends Component {

  constructor(props){
    super(props)
    this.state = {
      actors: []
    }
  }

  async componentDidMount() {
    await this.getActors();
    await auth0Client.load_jwts();
    await auth0Client.check_token_fragment();
  }

  getActors = () => {
    $.ajax({
      url: `https://castingya.herokuapp.com/actors`,
      type: "GET",
      success: (result) => {
        this.setState({
          actors: result.actors })
        return;
      },
      error: (error) => {
        alert('Unable to load actors. Please try your request again')
        return;
      }
    })
  }

  actorAction = (id) => (action) => {
    if(action === 'DELETE') {
      if(window.confirm('are you sure you want to delete this actor?')) {
        $.ajax({
          url: `https://castingya.herokuapp.com/actors/${id}`,
          headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
          type: "DELETE",
          success: (result) => {
            this.getActors();
          },
          error: (error) => {
            alert('Unable to delete the actor. Please try your request again')
            return;
          }
        })
      }
    }
  }

  updateActor = (id) => (event) => {
    event.preventDefault();
    $.ajax({
      url: `https://castingya.herokuapp.com/actors/${id}`,
      headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
      type: "PATCH",
      dataType: 'json',
      contentType: 'application/json',
      data: JSON.stringify({
        name: this.state.name,
        age: this.state.age,
        gender: this.state.gender,
      }),
      xhrFields: {
        withCredentials: true
      },
      crossDomain: true,
      success: (result) => {
        window.location.reload(false);
        return;
      },
      error: (error) => {
        alert('Unable to update the actor. Please try your request again')
        return;
      }
    })
  }

  handleChange = (event) => {
    this.setState({[event.target.name]: event.target.value})
  }

  showform = (location) => {
  let marker = this.state.actors.filter(m => m.id === location.id)[0];
  
	document.querySelector("#name").value = marker.name;
  document.querySelector("#age").value = marker.age;
  document.querySelector("#gender").value = marker.gender;
  const id = document.querySelector("#id").value = marker.id;
    $('.form-visible').show();
    $('.dborder3').hide();
    $('.button-visible').hide();
    this.setState({ id });
  }

	render() {
    const { id } = this.state;
		return (		
		<div id="display">		
		<div className="dborder boxshw cardwi">
    {
        auth0Client.can('post:actors') &&
        <Link to='/castingagency/addactor'>
    <button type="button" className='button-visible' title="add a new actor">Add a new actor</button>
        </Link>
    }
    <form className="form-view form-visible" onSubmit={this.updateActor(id)}>
              <label>
                Name
                <input type="text" name="name" id='name' onChange={this.handleChange} required />
              </label>
              <br></br>
              <label>
                Age
                <input type="number" name="age" id="age" onChange={this.handleChange} required />
              </label>
              <br></br>
              <label>
                Gender
                <input type="text" name="gender" id="gender" onChange={this.handleChange} required />
              </label>
              <br></br>
              <label>
                Id
                <input type="text" size='4' name="id" id="id" onChange={this.handleChange} readOnly />
              </label>
              <input type="submit" className="button" value="Save" />
            </form>
		{this.state.actors && this.state.actors.map((q, ind) => (
			<div key={q.id}>
			<div className="dborder3">	
			<ul className="listnone">
				<li><span className="listbold">Name</span>: <span id="name">{q.name}</span>
        {auth0Client.can('patch:actors') && <img src="../images/edit.png" alt="edit" title="Edit this actor" className="edit" onClick={() => this.showform(q)}></img>}
        </li>
				<li><span className="listbold">Age</span>: <span id="age">{q.age}</span></li>	
        <li><span className="listbold">Gender</span>: <span id="gender">{q.gender}</span>
        {auth0Client.can('delete:actors') && <img src="../images/delete.png" alt="delete" title="Delete this actor" className="delete" onClick={() => this.actorAction(q.id)('DELETE')}></img>}
        </li>			
			</ul>
			</div>
			</div>
          ))}
		</div>
		</div>
		);
	  }
  }

  export default Actor;