import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch, withRouter} from 'react-router-dom';
import './App.css';
import './responsive.css';
import Head from './components/header';
import Movie from './components/movies';
import Actor from './components/actors';
import SideBar from './components/sidebar';
import Foot from './components/footer';
import AddMovie from './components/addMovie';
import AddActor from './components/addActor';
import auth0Client from './components/Auth';
import Callback from './components/Callback';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checkingSession: true,
    }
  }
  async componentDidMount() {
    if (this.props.location.pathname === './components/callback') return;
    try {
      await auth0Client.silentAuth();
      this.forceUpdate();
    } catch (err) {
      if (err.error !== 'login_required') console.log(err.error);
    }
  }

  render() {
    return (
      <div>
        <Router>   
          <Head/>      
        <Switch>
          <Route path="/castingagency/" exact component={Movie} />
          <Route path="/castingagency/actors" component={Actor} />
          <Route path="/castingagency/addmovie" exact component={AddMovie} />
          <Route path="/castingagency/addactor" component={AddActor} />
          <Route exact path='/castingagency/callback' component={Callback}/>
          <Route component={Movie} />
        </Switch>
        <SideBar/>
        <Foot/>
        </Router>    
      </div>
    );
  }
}

export default withRouter(App);
