import React, { Component } from 'react';
import $ from 'jquery';
import '../FormView.css';
import auth0Client from './Auth';

class AddMovie extends Component {
  constructor(props){
    super();
    this.state = {
      title: "",
      release_date: "",
    }
  }

  submitMovie = (event) => {
    event.preventDefault();
    $.ajax({
      url: 'https://castingya.herokuapp.com/movies_new',
      headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
      type: "POST",
      dataType: 'json',
      contentType: 'application/json',
      data: JSON.stringify({
        title: this.state.title,
        release_date: this.state.release_date,
      }),
      xhrFields: {
        withCredentials: true
      },
      crossDomain: true,
      success: (result) => {
        document.getElementById("add-movie-form").reset();
        return;
      },
      error: (error) => {
        alert('Unable to add the movie. Please try your request again')
        return;
      }
    })
  }

  handleChange = (event) => {
    this.setState({[event.target.name]: event.target.value})
  }

  render() {
    return (
      <div id="add-form">
        <h2>Add a new movie</h2>
        <form className="form-view" id="add-movie-form" onSubmit={this.submitMovie}>
          <label>
            Title
            <input type="text" name="title" onChange={this.handleChange} required/>
          </label>
          <br></br>
          <label>
            Release date
            <input type="date" name="release_date" onChange={this.handleChange} required/>
          </label>
          <br></br>
          <input type="submit" className="button" value="Submit" />
        </form>
      </div>
    );
  }
}

export default AddMovie;
