import React, { Component } from 'react';
import $ from 'jquery';
import '../FormView.css';
import auth0Client from './Auth';

class AddActor extends Component {
  constructor(props){
    super();
    this.state = {
      name: "",
      age: "",
      gender: "",
    }
  }

  submitActor = (event) => {
    event.preventDefault();
    $.ajax({
      url: 'https://castingya.herokuapp.com/actors_new',                                        
      headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
      type: "POST",
      dataType: 'json',
      contentType: 'application/json',
      data: JSON.stringify({
        name: this.state.name,
        age: this.state.age,
        gender: this.state.gender,
      }),
      xhrFields: {
        withCredentials: true
      },
      crossDomain: true,
      success: (result) => {
        document.getElementById("add-actor-form").reset();
        return;
      },
      error: (error) => {
        alert('Unable to add the actor. Please try your request again')
        return;
      }
    })
  }

  handleChange = (event) => {
    this.setState({[event.target.name]: event.target.value})
  }

  render() {
    return (
      <div id="add-form">
        <h2>Add a new actor</h2>
        <form className="form-view" id="add-actor-form" onSubmit={this.submitActor}>
          <label>
            Name
            <input type="text" name="name" onChange={this.handleChange} required/>
          </label>
          <br></br>
          <label>
            Age
            <input type="number" name="age" onChange={this.handleChange} required/>
          </label>
          <br></br>
          <label>
            Gender
            <input type="text" name="gender" onChange={this.handleChange} required/>
          </label>
          <br></br>
          <input type="submit" className="button" value="Submit" />
        </form>
      </div>
    );
  }
}

export default AddActor;
