import React, { Component } from 'react';
import $ from 'jquery';
import auth0Client from './Auth';
import { Link } from 'react-router-dom';

class Movie extends Component {

  constructor(props){
    super(props)
    this.state = {
      movies: []
    }
  }

  async componentDidMount() {
    await this.getMovies();
    await auth0Client.load_jwts();
    await auth0Client.check_token_fragment();
  }

  getMovies = () => {
    $.ajax({
      url: `https://castingya.herokuapp.com/movies`,
      type: "GET",
      success: (result) => {
        this.setState({
          movies: result.movies })
        return;
      },
      error: (error) => {
        alert('Unable to load movies. Please try your request again')
        return;
      }
    })
  }

  movieAction = (id) => (action) => {
    if(action === 'DELETE') {
      if(window.confirm('are you sure you want to delete this movie?')) {
        $.ajax({
          url: `https://castingya.herokuapp.com/movies/${id}`,
          headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
          type: "DELETE",
          success: (result) => {
            this.getMovies();
          },
          error: (error) => {
            alert('Unable to delete the movie. Please try your request again')
            return;
          }
        })
      }
    }
  }

  updateMovie = (id) => (event) => {
    event.preventDefault();
    $.ajax({
      url: `https://castingya.herokuapp.com/movies/${id}`,
      headers: { 'Authorization': `Bearer ${auth0Client.getToken()}` },
      type: "PATCH",
      dataType: 'json',
      contentType: 'application/json',
      data: JSON.stringify({
        title: this.state.title,
        release_date: this.state.release_date,
      }),
      xhrFields: {
        withCredentials: true
      },
      crossDomain: true,
      success: (result) => {
        window.location.reload(false);
        return;
      },
      error: (error) => {
        alert('Unable to update the movie. Please try your request again')
        return;
      }
    })
  }

  handleChange = (event) => {
    this.setState({[event.target.name]: event.target.value})
  }

  showform = (location) => {
  let marker = this.state.movies.filter(m => m.id === location.id)[0];
  
	document.querySelector("#title").value = marker.title;
  document.querySelector("#release_date").value = marker.release_date;
  const id = document.querySelector("#id").value = marker.id;
    $('.form-visible').show();
    $('.dborder3').hide();
    $('.button-visible').hide();
    this.setState({ id });
  }

	render() {
    const { id } = this.state;
		return (		
		<div id="display">	
		<div className="dborder boxshw cardwi" id='someDivId'>
    {
        auth0Client.can('post:movies') &&
        <Link to='/castingagency/addmovie'>
    <button type="button" className='button-visible' title="add a new movie">Add a new movie</button>
        </Link>
    }
   <form className="form-view form-visible" onSubmit={this.updateMovie(id)}>
              <label>
                Title
                <input type="text" name="title" id='title' onChange={this.handleChange} required />
              </label>
              <br></br>
              <label>
                Release date
                <input type="date" name="release_date" id="release_date" onChange={this.handleChange} required />
              </label>
              <br></br>
              <label>
                Id
                <input type="text" size='4' name="id" id="id" onChange={this.handleChange} readOnly />
              </label>
              <input type="submit" className="button" value="Save" />
            </form>
		{this.state.movies && this.state.movies.map((q, ind) => (
			<div key={q.id}>
			<div className="dborder3">	
			<ul className="listnone">
				<li><span className="listbold">Title</span>: <span>{q.title}</span>
        {auth0Client.can('patch:movies') && <img src="../images/edit.png" alt="edit" title="Edit this movie" className="edit" onClick={() => this.showform(q)}></img>}
        </li>
        <li><span className="listbold">Release date</span>: <span>{q.release_date}</span>
        {auth0Client.can('delete:movies') && <img src="../images/delete.png" alt="delete" title="Delete this movie" className="delete" onClick={() => this.movieAction(q.id)('DELETE')}></img>}
        </li>
			</ul>
			</div>
			</div>
          ))}
		</div>
		</div>
		);
	  }
  }

  export default Movie;