import React, { Component } from 'react';
import auth0Client from './Auth';
import { Link } from 'react-router-dom';

class SideBar extends Component {
	render(props) {
		const signOut = () => {
			auth0Client.signOut();
			props.history.replace('/');
		  };
		return (						
				<div className='options-box'>
					<Link to='/casting/'>		
						<div align="center" role="listitem" tabIndex="4" className="sidebar-item"> 
							<strong>Movies</strong>
							<br/>
							<img src="../images/movies.png" alt="movies"></img>
							</div>
							</Link>
							<Link to='/casting/actors'>
							<div align="center" role="listitem" tabIndex="5" className="sidebar-item"> 
							<strong>Actors</strong>
							<br/>
							<img src="../images/actors.jpg" alt="actors"></img>
							</div>
							</Link>	 
		<div align="center" role="listitem" tabIndex="6" className="sidebar-item"> 
							{
        !auth0Client.isAuthenticated() &&
        <button onClick={auth0Client.signIn}>Sign In</button>
      }
      {
        auth0Client.isAuthenticated() &&
        <div>
          <button onClick={() => {signOut()}}>Sign Out</button>
		  <label>{auth0Client.getProfile().name}</label>
        </div>
      }
	</div>
		 </div>
		);
	  }
  }

  export default SideBar;